import React from 'react'
import cx from 'classnames'
import PropTypes from 'prop-types'
import './style.scss'

export default function NineSlice (props) {
  const {url, slice} = props
  const borderStyle = {
    borderImage: `url(${url}) ${slice.join(' ')} fill`,
    borderWidth: slice.map(v => v + 'px').join(' ')
  }
  return (
    <div style={props.style} className={cx('dui-nineslice', props.className)}>
      <div style={borderStyle} className='dui-nineslice-border' />
      <div className='dui-nineslice-content'>
        {props.children}
      </div>
    </div>
  )
}

NineSlice.propTypes = {
  url: PropTypes.string.isRequired,
  slice: PropTypes.arrayOf(PropTypes.number).isRequired
}
